import React from 'react';
import { Layout, Menu } from 'antd';
import { withRouter } from 'react-router-dom';
import { UiRestriction } from '../components';
import { UseContext } from '../hoc';
import { UserContext } from '../contexts';

const { Header, Content, Footer } = Layout;
const layoutConfig = [
  {
    name: 'Home',
    link: '/',
  }, {
    name: 'Alternaors',
    link: '/alternators',
  }, {
    name: 'Spare Parts',
    link: '/spare'
  }, {
    name: 'Login',
    link: '/login',
    hidden: true
  }, {
    name: 'Logout',
    link: '/logout',
    requireLogin: true,
  }
]

class MainLayout extends React.Component {
  onMenuItemClick = ({ key }) => {
    if (key === '/logout') {
      const { logOut } = this.props;
      logOut()
    } else {
      const { history } = this.props;
      history.push(key);
    }
  }

  render() {
    const { children, location } = this.props;
    return (
      <Layout>
        <Header style={{ position: 'fixed', zIndex: 3, width: '100%' }}>
          <div className="logo" />
          <Menu 
            theme="dark" 
            mode="horizontal" 
            defaultSelectedKeys={[location.pathname]}
            onClick={this.onMenuItemClick}
          >
            {layoutConfig.map(config =>
              <Menu.Item key={config.link}><UiRestriction {...config}>{config.name}</UiRestriction></Menu.Item>
            )}
          </Menu>
        </Header>
        <Content className="site-layout" style={{ padding: '0 50px', marginTop: 64 }}>
          {/* <Breadcrumb style={{ margin: '16px 0' }}>
            <Breadcrumb.Item>Home</Breadcrumb.Item>
            <Breadcrumb.Item>List</Breadcrumb.Item>
            <Breadcrumb.Item>App</Breadcrumb.Item>
          </Breadcrumb> */}
          <div className="site-layout-background" style={{ padding: 24 }}>
            {children}
          </div>
        </Content>
        <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer>
      </Layout>
    )
  }
}

export default withRouter(UseContext(UserContext)(MainLayout));