import React from 'react';
import { UserContext } from '../contexts';

export const UiRestriction = ({ name, link, requireLogin = false, requireAdmin = false, hidden = false, children, ...rest }) => (
  <UserContext.Consumer>
    {({ userInfo }) => {
      if (!userInfo) {
        if (requireLogin || requireAdmin) {
          return <></>;
        }
        return <>{children}</>;
      } else {
        if ((requireAdmin && !userInfo.isAdmin) || hidden) {
          return (<></>)
        }

        return <>{children}</>;
      }
    }}
  </UserContext.Consumer>
)