import React, { lazy, Suspense } from 'react';
import { Switch, Route } from 'react-router-dom';
import { Redirect } from 'react-router';
import { PrivateRoute } from './router';
import { AddAlternator } from './pages';
const Login = lazy(() => import('./pages/login'));
const LandingPage = lazy(() => import('./pages/landingPage'));
const AlternatorListing = lazy(() => import('./pages/alternatorListing'));
// const AddAlternator = lazy(() => import('./pages/addAlternator'));

const renderLoader = () => <p>Loading</p>;

export default () => (
  <Switch>
    <Route
      exact
      path="/"
      render={props => <Suspense fallback={renderLoader()}><LandingPage {...props} /></Suspense>}
    />
    <Route
      exact
      path="/login"
      render={props => <Suspense fallback={renderLoader()}><Login {...props} /></Suspense>}
    />
    <Route
      exact
      path="/alternators"
      render={props => <Suspense fallback={renderLoader()}><AlternatorListing {...props} /></Suspense>}
    />
    <PrivateRoute 
      exact
      path="/alternators/add"
      render={props => <Suspense fallback={renderLoader()}><AddAlternator {...props} /></Suspense>}
    />
    <Redirect to="/"/>
  </Switch>
)